body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
### Primary
- Scissors Gradient: hsl(39, 89%, 49%) to hsl(40, 84%, 53%)
- Paper Gradient: hsl(230, 89%, 62%) to hsl(230, 89%, 65%)
- Rock Gradient: hsl(349, 71%, 52%) to hsl(349, 70%, 56%)
- Lizard Gradient: hsl(261, 73%, 60%) to hsl(261, 72%, 63%)
- Cyan: hsl(189, 59%, 53%) to hsl(189, 58%, 57%)
*/
button::-moz-focus-inner {
  border: 0; }

input,
textarea,
button,
select,
a,
label {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

/* COLORS */
/* ====================
        Mixins
=======================
*/
html {
  height: 100%;
  overflow: hidden;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#1f3756), to(#141539));
  background-image: linear-gradient(#1f3756, #141539); }

body {
  height: 100%;
  font-family: 'Barlow Semi Condensed', sans-serif;
  color: white;
  overflow: auto; }

#root {
  height: 100%; }

/* ====================
      Header Div
=======================
*/
.header {
  width: 50%;
  margin: 30px 0;
  grid-area: header;
  display: grid;
  justify-self: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-template-areas: "title points";
  border: 3px solid #606e85;
  border-radius: 1rem;
  font-weight: 700;
  padding: 20px;
  padding-top: 10px; }

.title {
  grid-area: title;
  justify-self: left;
  align-self: end; }
  .title .logoImg {
    height: calc(20px + 6vw); }

.pointsContainer {
  grid-area: points;
  border-radius: 5px;
  justify-self: right;
  padding: 10px 40px 0 40px;
  background-color: #fff;
  font-weight: 600;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: flex-start; }

.points-title,
.points-count {
  margin: 0;
  padding: 0; }

.points-title {
  color: #2a46c0;
  font-size: calc(8px + .4vw); }

.points-count {
  text-align: center;
  color: #3b4363;
  font-size: calc(10px + 5vw); }

/* ============================
    Choose Vertion of Game Div
===============================
*/
.chooseVersionOfGame {
  max-width: 350px;
  width: calc(95% - 1vw);
  display: flex;
  justify-self: center;
  justify-content: space-between;
  padding-bottom: 25px;
  /* Rounded sliders */ }
  .chooseVersionOfGame .choosedVersion {
    align-self: center; }
  .chooseVersionOfGame .versionSwitch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px; }
  .chooseVersionOfGame .versionSwitch input {
    opacity: 0;
    width: 0;
    height: 0; }
  .chooseVersionOfGame .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s; }
  .chooseVersionOfGame .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s; }
  .chooseVersionOfGame input:checked + .slider {
    background-color: #2196F3; }
  .chooseVersionOfGame input:focus + .slider {
    box-shadow: 0 0 1px #2196F3; }
  .chooseVersionOfGame input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px); }
  .chooseVersionOfGame .slider.round {
    border-radius: 34px; }
  .chooseVersionOfGame .slider.round:before {
    border-radius: 50%; }

/* =========================================
    Choose Option Div and Classic Version Div
=============================================
*/
.classicVersionContainer {
  background: url(/Rock-Paper-Scissors-Game/static/media/bg-triangle.1fd44bdf.svg) no-repeat center; }

/* The Biggest Border Creation */
.paperElement,
.rockElement,
.scissorsElement,
.spockElement,
.lizardElement {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  padding: 20px;
  display: flex;
  box-shadow: inset 0px -8px 0px 2px rgba(0, 0, 0, 0.5); }

/* Color of the Biggest Borders and Position */
.paperElementClassic {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-150%);
          transform: translateX(-150%); }

.rockElementClassic {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-25%);
          transform: translateX(-50%) translateY(-25%); }

.scissorsElementClassic {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(50%) translateY(-100%);
          transform: translateX(50%) translateY(-100%); }

.paperElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#4865f4), to(#5671f5));
  background-image: linear-gradient(#4865f4, #5671f5); }

.rockElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#dc2e4e), to(#dd405d));
  background-image: linear-gradient(#dc2e4e, #dd405d); }

.scissorsElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ec9e0e), to(#eca922));
  background-image: linear-gradient(#ec9e0e, #eca922); }

.spockElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#40b9ce), to(#52bed1));
  background-image: linear-gradient(#40b9ce, #52bed1); }

.lizardElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#834fe3), to(#8c5de5));
  background-image: linear-gradient(#834fe3, #8c5de5); }

/* Desgin, positions for the white circle with icon inside Element */
.chooseElementButton {
  cursor: pointer;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  background-color: white;
  border: 0px;
  margin: auto;
  outline: none;
  box-shadow: inset 0px 8px 0px -2px rgba(0, 0, 0, 0.1); }
  .chooseElementButton:hover {
    box-shadow: inset 0px 0px 15px rgba(0, 0, 0, 0.5); }
  .chooseElementButton:focus {
    box-shadow: inset 0px 0px 15px rgba(0, 0, 0, 0.9); }

/*===================
  Container of Rules
=====================*/
.rulesContainer {
  display: flex;
  justify-content: flex-end;
  margin-right: 80px; }

/*===============================
  Challenge and Who Made It Div
================================*/
.attribution {
  padding-top: 20px;
  text-align: center;
  /* Links styles */ }
  .attribution .attributionLink:link,
  .attribution .attributionLink:visited {
    color: red;
    text-decoration: none; }
  .attribution .attributionLink:hover {
    color: white; }

/*=============================
          Media Queries 
 ============================*/
@media only screen and (max-width: 768px) {
  .header {
    margin: 10px 0;
    padding: 10px; }
  .chooseVersionOfGame {
    padding-bottom: 0px; }
  .chooseOption {
    padding-top: 50px;
    padding-bottom: 40px; }
  .paperElementClassic {
    -webkit-transform: translateX(-160%) translateY(-40%);
            transform: translateX(-160%) translateY(-40%); }
  .scissorsElementClassic {
    -webkit-transform: translateX(60%) translateY(-140%);
            transform: translateX(60%) translateY(-140%); }
  .rockElementClassic {
    -webkit-transform: translateX(-50%) translateY(-12%);
            transform: translateX(-50%) translateY(-12%); }
  .paperElement,
  .rockElement,
  .scissorsElement,
  .spockElement,
  .lizardElement {
    width: calc(55px + 1vw);
    height: calc(55px + 1vw); }
  .paperImg,
  .rockImg,
  .scissorsImg,
  .spockImg,
  .lizardImg {
    height: calc(30px + 1vw); }
  .chooseElementButton {
    width: calc(55px + 1vw);
    height: calc(55px + 1vw); }
  .pointsContainer {
    padding: 0px 20px 0 20px; }
  .rulesContainer {
    justify-content: center;
    margin-right: 0px; }
  .attribution {
    padding-bottom: 5px; } }

