===========================*/
.rulesOpenButton {
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 10px 40px;
  background-color: transparent;
  color: white;
  border: 1px solid white;
  border-radius: 9px;
  outline: none;
  font-weight: 700;
  cursor: pointer; }
  .rulesOpenButton:hover {
    background-color: white;
    color: #3b4363; }

/*
============================
        Menu of Rules
===========================*/
.rulesMenu {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  padding: 20px 30px;
  background-color: white;
  border-radius: 1%;
  color: #3b4363;
  display: flex;
  flex-flow: column;
  -webkit-animation: showRules .5s linear;
          animation: showRules .5s linear; }
  .rulesMenu .rulesCloseButton {
    cursor: pointer;
    background-color: transparent;
    border: 0px; }

/*
============================
    Animation of Menu
===========================*/
@-webkit-keyframes showRules {
  0% {
    top: 150%; }
  50% {
    top: 100%; }
  100% {
    top: 50%; } }
@keyframes showRules {
  0% {
    top: 150%; }
  50% {
    top: 100%; }
  100% {
    top: 50%; } }

